<script>
import WriteLetter from '@/components/WriteLetter.vue'

export default {
  name: 'Contacts',
  components: {
    WriteLetter,
  },
  data() {
		return {
			msg: '',
		}
	},
  methods: {
    copyToClipboard: function({target}) {
      const copyText = target.textContent.trim();

      if (navigator.clipboard) {
        navigator.clipboard.writeText(copyText)
          .then(() => {
            this.msg = 'Email адрес скопирован в буфер обмена'
          })
          .catch(err => {
            this.msg = `Something went wrong ${err}`;
          });
      } else {
        document.execCommand("copy");
      }
    }
  }
}
</script>

<style lang="scss">
  .contacts {
    &__content {
      display: grid;

      @media (max-width: 850px) {
        grid-template-columns: 1fr;
      }

      @media (min-width: 850px) {
        grid-template-columns: repeat(2, 1fr);
      } 
    }

    &__list {
      text-align: center;
    }

    &__item {
      @media (max-width: 360px) {
        display: flex;
        flex-direction: column;
      }
    }

    &__item-name {
      @media (max-width: 360px) {
        margin-bottom: 0.25em;
      }
    }

    &__item-link {
      cursor: pointer;
      transition: .3s;

      &:hover {
        color: var(--color-link);
      }
    }

    &__icons {
      display: flex;
      justify-content: center;
    }

    &__icon {
      position: relative;

      width: 2em;
      padding-top: 2em;
      margin: 1em;

      transition: .3s;
      cursor: pointer;

      &:hover {
        transform: scale(1.2);
      }
    }

    &__icon-svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      width: 100%;
      height: 100%;

      .contacts__icon--git:hover & {
        fill: #823f9a;
      }
      .contacts__icon--tg:hover & {
        fill: #2da1d3;
      }
      .contacts__icon--vk:hover & {
        fill: #4e7db2;
      }
    }

    &__copy-clipboard {
      position: relative;

      &::after {
        content: attr(data-msg);

        position: absolute;
        top: 125%;
        left: 0px;
        z-index: 50;

        display: none;

        padding: 0.5em 0.75em;

        border-radius: 5px;
        box-shadow: 0 0 7px var(--main-color);
        background-color: var(--second-bg);
        color: initial;
      }
      &:focus::after {
        display: block;
      }
    }
  }
</style>